<template>
    <Transition name="modal">
  <div class="modal" @click="closeModal">
    <div @click.stop="" class="modal__content">
        <h2 class="modal__content-title">Отправить картинку</h2>
        <label>
            <input v-model="URL" type="text">
            <span>URL</span>
        </label>
        <label>
            <input v-model="comment" type="text">
            <span>Комментарий</span>
        </label>
        <div class="modal__content-btns">
            <button @click="closeModal" class="modal__content-btn cancel">Отмена</button>
            <button @click="sendPicture" class="modal__content-btn send">ОТПРАВИТЬ</button>
        </div>
    </div>
  </div>
    </Transition>
</template>

<script>
export default {

    data() {
        return {
            URL: '',
            comment: '',
        }
    },

    props: {
        user: Object
    },

    methods: {
        closeModal() {
            this.$emit('closeModal')
        },
        
        sendPicture() {
            if(this.URL.startsWith('https://')) {
                const pictureInfo = {
                    author: this.user.name,
                    URL: this.URL,
                    comment: this.comment,
                    date: new Date().toLocaleTimeString().slice(0, -3)
                }
                this.$emit('sendPicture', pictureInfo)
                console.log(pictureInfo);
                this.URL = ''
                this.comment = ''
                this.closeModal()
            }
        }
    }
}
</script>