<template>
  <div class="chat">
    <ChatHeader 
      :user="user"
      :typeInfo="typeInfo"
    />
    <ChatMain 
      :messanges="messanges"
      :user="user"
    />
    <ChatFooter
      :user="user"
      @sendMessange="sendMessange"
      @changeStatus="changeStatus"
      @openModal="openModal"
    />
  </div>
</template>

<script>

import ChatHeader from '@/components/ChatHeader.vue'
import ChatMain from '@/components/ChatMain.vue'
import ChatFooter from '@/components/ChatFooter.vue'

export default {

    components: {
        ChatHeader,
        ChatMain,
        ChatFooter
    },

    methods: {
      sendMessange(messange) {
        this.$emit('sendMessange', messange)
      },

      changeStatus(text) {
        this.$emit('changeStatus', text)
      },

      openModal(user) {
        this.$emit('openModal', user)
      }
    },

    props: {
      user: Object,
      messanges: Array,
      typeInfo: Array
    }

}
</script>

