<template>
    <div :class="'chat__main-' + (messange.author === user.name ? 'sendContent' : 'receiveContent')">
        <span
            v-if="messange.author === user.name"
            :class="'chat__main-' + (messange.author === user.name ? 'sendTime' : 'receiveTime')">
            {{ messange.date }}
        </span>
        <div 
             v-if="messange.text"
            :class="'chat__main-' + (messange.author === user.name ? 'sendMessange' : 'receiveMessange')">
            {{messange.text}}
        </div>
        <div 
             v-else
            :class="'chat__main-' + (messange.author === user.name ? 'sendMessange' : 'receiveMessange')">
            <img 
                :class="'chat__main-' + (messange.author === user.name ? 'sendPicture' : 'receivePicture')" 
                :src="messange.URL" alt="">
            <p v-if="messange.comment">{{messange.comment}}</p>
        </div>

        <span 
            v-if="messange.author !== user.name"
            :class="'chat__main-' + (messange.author === user.name ? 'sendTime' : 'receiveTime')">
            {{ messange.date }}
        </span>
    </div>
</template>

<script>
export default {
    data() {
        return {
            
        }
    },

    props: {
        messange: Object,
        user: Object
    }
}
</script>

