<template>
  <div class="wrapper">
    <Chat v-for="user in users" :key="user.id"
      :user="user"
      :id="id"
      @sendMessange="sendMessange"
      :messanges="messanges"
      @changeStatus="changeStatus"
      :typeInfo="typeInfo"
      @openModal="openModal"
    />
    <Modal 
      v-show="isModalOpen" 
      :user="user"
      @closeModal="closeModal"
      @sendPicture="sendPicture"
    />
  </div>
</template>

<script>
import Chat from '@/components/Chat.vue'
import Modal from '@/components/Modal.vue'

export default {

  data() {

    return {
      isModalOpen: false,
      user: {},
      currentId: 1,
      typeInfo: [],
      messanges: [],
      isModalOpen: false,
      users: [
        {id: 1, name: 'Александр', photo: 'https://w7.pngwing.com/pngs/862/646/png-transparent-beard-hipster-male-man-avatars-xmas-giveaway-icon-thumbnail.png'},
        {id: 2, name: 'Евгений', photo: 'https://www.pngkey.com/png/detail/805-8050143_legalbot-avatar-avatar-bot.png'}
      ]
    }
  },

  components: {
    Chat,
    Modal
  },

  methods: {

    sendMessange(messange) {
      messange.id = this.currentId++,
      this.messanges.push(messange);
      console.log(messange);
    },

    changeStatus(text) {
      text[1] != '' ? this.typeInfo = [text[0], true] : this.typeInfo = [text[0], false]
      console.log(text);
    },

    openModal(user) {
      this.user = user
      this.isModalOpen = true
      console.log(this.user);
    },

    closeModal() {
      this.isModalOpen = false
    },

    sendPicture(pictureInfo) {
      pictureInfo.id = this.currentId++;
      this.messanges.push(pictureInfo)
    }

  },
}
</script>
