<template>
  <footer class="chat__footer">
    <input @input="changeStatus" v-model='text' type="text" class="chat__footer-input" placeholder="Написать сообщение...">
    <button @click="openModal" v-if="text === ''" class="chat__footer-btn">
        <img src="@/assets/images/Photo.svg" alt="">
    </button>
    <button @click="sendMessange" v-else class="chat__footer-btn">
        <img src="@/assets/images/Send.svg" alt="">
    </button>
  </footer>
</template>

<script>
export default {
  data() {
    return {
      text: '',
      messange: {},
      isTyping: false,
    }
  },

  props: {
    user: Object,
  },

  methods: {

    openModal() {
      this.$emit('openModal', this.user)
    },

    sendMessange() {
      this.messange.author = this.user.name
      this.messange.text = this.text
      this.messange.date = new Date().toLocaleTimeString().slice(0, -3)

      this.$emit('sendMessange', this.messange)
      this.text = ''
      this.messange = {}
      this.changeStatus()
    },

    changeStatus() {
      this.text ? this.isTyping = true : this.isTyping = false;
      const typeInfo = [this.user.name, this.isTyping]
      this.$emit('changeStatus', typeInfo)
    }
  }
}
</script>

